.diagram {
  position: relative;
}

.diagram-overlay {
  width: 70%;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  background-color: white;
  z-index: 10;
  -webkit-box-shadow: -5px 0 10px -2px #888;
  box-shadow: -5px 0 10px 0px #888;
}

