.logout-button {
  &:hover {
    background-color: $state-danger-text !important;
  }
}

.logo {
  background-image: url("logo/logo-medium.gif");
  background-repeat: no-repeat;
  background-clip: content-box;
  background-size: contain;
  height: 30px;
  width: 500px;
}

.control-group {
  border-style: dashed;
  border-width: thin;
  border-color: $brand-primary;
  margin: 10px;
  padding: 5px;

  .js-control-drag-handle {
    background-color: $brand-primary;
    color: white;
    width: 100%;
    padding: 1px;
  }
}

.text-white {
  color: white !important;
}

